<template>
  <div id="productCenter">
    <div class="list1">
      <div class="title_1">{{title.t1}}</div>
      <div class="title_2">{{title.t2}}</div>
    </div>
    <div class="list2">
      <div class="l2-1">
        <ul class="clearfloat">
          <li @click="navbtn(index,item.id)" v-for="(item,index) in navBtn" :key="index" :class="{sel:index == idx}">{{item.name}}</li>
        </ul>
      </div>
      <div class="l2-2">
        <div class="swiper-container swiper02">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(item,index) in iconDetail" :key="index" @click="select(index)">
              <div>
                <router-link :to="{path:'/productCenter/ProductList',query:{item:item}}">
                  <div class="slide1" :class="{default:item.pic == ''}">
                    <img :src="$baseLink+item.pic" alt="">
                  </div>
                  <div class="slide2">
                    <span>{{item.name}}</span>
                  </div>
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
        <!--  page1,page2 -->
      </router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive">
      <!--  page3 -->
    </router-view>
  </div>
</template>

<script>
    export default {
      name: "productCenter",
      data(){
        return {
          title:{
            t1:'',
            t2:'80'
          },
          navBtn:[],
          idx:0, //  
          iconDetail:[],
          swiper02:null,
          length:0
        }
      },
      methods:{
        navbtn(i,id){
          const that = this;
          this.idx = i;
          // console.log(id);
          let defaultId = id;
          // 
          that.$api.axiosGet('/index/product/getCates/pid/'+id,{},function (data) {
            // console.log(data);
            that.iconDetail = data.data.cates.all;
            that.length = that.iconDetail.length;
            var w = $('.swiper02').width();
            if(that.length<8){
              console.log(that.length);
              var l = 70.5*(8-that.length);
              $('.swiper02').css({
                '-webkit-transform': 'translateX('+l+'px)',
                '-moz-transform': 'translateX('+l+'px)',
                '-ms-transform': 'translateX('+l+'px)',
                '-o-transform': 'translateX('+l+'px)',
                'transform': 'translateX('+l+'px)',
                // 'margin-left': -w/2+'px'
              })
            }else{
              $('.swiper02').css({
                '-webkit-transform': 'translateX(0px)',
                '-moz-transform': 'translateX(0px)',
                '-ms-transform': 'translateX(0px)',
                '-o-transform': 'translateX(0px)',
                'transform': 'translateX(0px)',
                // 'margin-left': -w/2+'px'
              })
            }
            $('.l2-2 .swiper-slide').removeClass('sel');
            $('.l2-2 .swiper-slide').eq(0).addClass('sel');
            setTimeout(function () {
              that.swiper02.slideTo(0);
            },50)
            that.$router.push({
              name: 'ProductInfo',
              query:{
                id:defaultId
              }
            });
          })
        },
        select(i){
          $('.l2-2 .swiper-slide').removeClass('sel');
          $('.l2-2 .swiper-slide').eq(i).addClass('sel');
        }
      },
      updated(){
        const that = this;
      },
      mounted(){
        const that = this;
        // 
        this.$api.axiosGet('/index/product/getCates/pid/0',{},function (data) {
          console.log(data);
          that.navBtn = data.data.cates;
          let defaultId = data.data.cates[0].id;
          that.$router.push({
            name: 'ProductInfo',
            query:{
              id:defaultId
            }
          });
          // 
          that.$api.axiosGet('/index/product/getCates/pid/'+data.data.cates[0].id,{},function (data) {
            console.log(data);
            that.iconDetail = data.data.cates.all;
            that.length = that.iconDetail.length;
            if(that.length<8){
              console.log(that.length);
              var l = 70.5*(8-that.length);
              $('.swiper02').css({
                '-webkit-transform': 'translateX('+l+'px)',
                '-moz-transform': 'translateX('+l+'px)',
                '-ms-transform': 'translateX('+l+'px)',
                '-o-transform': 'translateX('+l+'px)',
                'transform': 'translateX('+l+'px)',
                // 'margin-left': -w/2+'px'
              })
            }else{
              $('.swiper02').css({
                '-webkit-transform': 'translateX(0px)',
                '-moz-transform': 'translateX(0px)',
                '-ms-transform': 'translateX(0px)',
                '-o-transform': 'translateX(0px)',
                'transform': 'translateX(0px)',
                // 'margin-left': -w/2+'px'
              })
            }
            //
            setTimeout(function () {
              that.swiper02 = new Swiper('.swiper02',{
                slidesPerView: 8,
                spaceBetween: 10,
                observer:true,
                observeParents:false,//swiperswiper
                onSlideChangeEnd: function(swiper){
                  swiper.update();
                  // that.swiper02.startAutoplay();
                  // that.swiper02.reLoop();
                }
              })
            },50);
          })
        })
      }
    }
</script>

<style scoped>
@import "../../static/css/productCenter.css";
</style>
<style>
  @import "../../static/css/public.css";
</style>



